const YoutubeValidate = (yt_url) => {
    let url = yt_url.split("\"");
    for(let y = 0; y<url.length; y++) {
        if(url[y].slice(0, 23) == "https://www.youtube.com"){
            return url[y];
        }
    }
    return false;
}

const GmapValidate = (gm_url) => {
    let url = gm_url.split("\"");
    for(let g = 0; g<url.length; g++) {
        if(url[g].slice(0, 18) == "https://www.google"){
            return url[g];
        }
    }
    return false;
}
export { YoutubeValidate, GmapValidate };