import React, { useState, useCallback } from 'react';
import Cropper from 'react-easy-crop';


const ImageCropper = ({setProp_images, prop_images, oldImgLength})=> {
    const [crop, setCrop] = useState({ x: 0, y: 0 });
    const [image, setImage] = useState(null);
    const [imgResult, setImgResult] = useState([]);
    const onCropComplete = useCallback((croppedArea, croppedAreaPixels) => {
        setImage(croppedAreaPixels);
    }, []);
    const [src, selectFile] = useState(null);
    const [errors, setErrors] = useState('');
    const [imgFile, setImgFile] = useState(null);
    const handleFileChange = (e) => {
        if(e.target.files[0]){
            if(e.target.files[0].type === "image/jpeg" || e.target.files[0].type === "image/png" ){
                if(e.target.files[0].size < 3145728) {
                    let img = document.createElement('img');
                    let blob = URL.createObjectURL(e.target.files[0]);
                    img.src = blob;
                    img.onload = function() {
                        let w = img.width;
                        let h = img.height;
                        if(w > 500 && h >350) {
                            selectFile(URL.createObjectURL(e.target.files[0]));
                            setImgFile(e.target.files[0]);
                            setErrors('');
                        } else {
                            selectFile(null);
                            setErrors('Image is too small');
                            setImgFile(null);
                        }
                    }
                } else {
                    selectFile(null);
                    setErrors('Image is too big');
                    setImgFile(null);
                }
            } else {
                selectFile(null);
                setErrors('Only png and jpg allowed');
                setImgFile(null);
            }
        }
        
    }
    const onAddImg = () => {
        selectFile(null);
        setImgFile(null);

        let img = document.createElement('img');
        img.src = src;
        img.onload = function() {
            const canvas = document.createElement("canvas");
            canvas.width = image.width;
            canvas.height = image.height;
            const ctx = canvas.getContext("2d");

            ctx.drawImage(
                img,
                image.x, image.y,
                image.width, image.height,
                0, 0,
                image.width, image.height
            );
            const base64Image = canvas.toDataURL("image/jpeg");
            setImgResult(oldImages => [...oldImages,{imgSrc:base64Image}]);
            setProp_images(oldArray => [...oldArray, {prop_img:imgFile, posX:image.x, posY:image.y, crWidth:image.width, crHeight:image.height, imgSrc:base64Image}]);
        }
    }
    const removeImg = (e) => {
        const imgSrc = e.target.getAttribute("name");
        setImgResult(imgResult.filter(item => item.imgSrc !== imgSrc));
        setProp_images(prop_images.filter(item => item.imgSrc !== imgSrc));
        selectFile(null);
        setImgFile(null);
    }
    const localImages = imgResult.map((imgSR, i) => {
        return (
            <span className='rounded border border-primary' key={i}><img src={imgSR.imgSrc} alt={"image "+i} className='cropped_image_res rounded' /><span className='close_img' name={imgSR.imgSrc} onClick={removeImg}>&times;</span></span>
        );
    });
    return (
        <>
            {(imgResult.length + oldImgLength)>0?<div className='added_img_list'>
                {localImages}
            </div>:""}
            {(imgResult.length + oldImgLength)<5?<div className='form-row'>
                <div className='form-group col-md-12'>
                    <div className="custom-file">
                        <input id="file" type="file" className="custom-file-input" accept="image/*" onChange={handleFileChange} />
                        <label className="custom-file-label" htmlFor="file">Choose image</label>
                        <small>Minimum size of image is 500x350 in px</small>
                        <span className="form_err">{errors?errors:""}</span>
                    </div>
                </div>
            </div>:<p className='maximum_img'>
                Maximum You can upload upto 5 images
            </p>}
            {src?<div className='image_cropper_box'>
                <p>Reposition and crop the image</p>
                <div className="image_crop_inner">
                    <div className="crop-container">
                        <Cropper
                        image={src}
                        crop={crop}
                        zoom={1}
                        aspect={10 / 7}
                        onCropChange={setCrop}
                        onCropComplete={onCropComplete}
                        objectFit = "horizontal-cover"
                        />
                    </div>
                </div>
                <button className='btn btn-outline-primary' onClick={onAddImg}>Crop Image and add to queue</button>
            </div>:""}
            {/* <button onClick={testApi}>Test Api</button> */}
        </>
    )
}
export default ImageCropper;